import React from "react";
import { Container } from "reactstrap";
import Slider from "react-slick";

import { sliderData1 } from "../../assets/fake-data/slider1";
import "./slider.css";

const HeroSlider1 = () => {
  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
  };
  return (
    <section>
      <Container>
        <Slider {...settings}>
          {sliderData1.map((item) => (
            <div key={item.id}>
                <div class="slider__img1 ">
                  <img src={item.imgUrl} height="100%" width="100%" />
                </div>
              </div>
          ))}
        </Slider>
      </Container>
    </section>
  );
};

export default HeroSlider1;
