import React, { useState, useEffect } from "react";

import { Container, Row, Col } from "reactstrap";

import ProductCard from "../product-card/ProductCard";
import {
  CakeProducts,
  NonVegProducts,
  KebabsProducts,
  FastFoodProducts,
  SweetsProducts,
  BrowniesProducts,
  TubCakesProducts,
  SandwichesProducts,
  DrinksProducts,
  GiftsProducts,
} from "../../assets/fake-data/products";
import "./menu-pack.css";

const MenuPack = () => {
  const [filter, setFilter] = useState("Non Veg Corner");
  const [products, setProducts] = useState(NonVegProducts);

  useEffect(() => {
    if (filter === "Cake Corner") {
      setProducts(CakeProducts);
    }
    if (filter === "Kebabs Corner") {
      setProducts(KebabsProducts);
    }
    if (filter === "Non Veg Corner") {
      setProducts(NonVegProducts);
    }

    if (filter === "Fast Food Corner") {
      setProducts(FastFoodProducts);
    }

    if (filter === "Sweets Corner") {
      setProducts(SweetsProducts);
    }

    if (filter === "Brownies Corner") {
      setProducts(BrowniesProducts);
    }

    if (filter === "Tub Cakes Corner") {
      setProducts(TubCakesProducts);
    }

    if (filter === "Sandwiches Corner") {
      setProducts(SandwichesProducts);
    }

    if (filter === "Drinks Corner") {
      setProducts(DrinksProducts);
    }
    if (filter === "Gift Corner") {
      setProducts(GiftsProducts);
    }
  }, [filter]);

  return (
    <section>
      <Container>
        <Row>
          <Col lg="12" className="text-center mb-4">
            <h3 className="menu__title">Our Menu</h3>
          </Col>
          <Col lg="12" className="text-center mb-5">
          
            <button
              className={`filter-btn ${
                filter === "Non Veg Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Non Veg Corner")}
            >
              Non Veg Corner
            </button>
            <button
              className={`filter-btn ${
                filter === "Kebabs Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Kebabs Corner")}
            >
              Kebabs Corner
            </button>
            <button
              className={`filter-btn ${
                filter === "Fast Food Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Fast Food Corner")}
            >
              Fast Food Corner
            </button>
            <button
              className={`filter-btn ${
                filter === "Sweets Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Sweets Corner")}
            >
              Sweets Corner
            </button>
            <button
              className={`filter-btn ${
                filter === "Brownies Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Brownies Corner")}
            >
              Brownies Corner
            </button>
            <button
              className={`filter-btn ${
                filter === "Tub Cakes Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Tub Cakes Corner")}
            >
              Tub Cakes Corner
            </button>
            <button
              className={`filter-btn ${
                filter === "Sandwiches Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Sandwiches Corner")}
            >
              Sandwiches Corner
            </button>
            <button
              className={`filter-btn ${
                filter === "Drinks Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Drinks Corner")}
            >
              Drinks Corner
            </button>
            <button
              className={`filter-btn ${
                filter === "Cake Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Cake Corner")}
            >
              Cake Corner
            </button>
            <button
              className={`filter-btn ${
                filter === "Gift Corner" ? "active__btn" : ""
              }`}
              onClick={() => setFilter("Gift Corner")}
            >
              Gift Corner
            </button>
          </Col>

          {products.map((item) => (
            <Col lg="3" md="4" sm="6" xs="6" key={item.id} className="mb-4">
              {" "}
              <ProductCard item={item} />
            </Col>
          ))}
        </Row>
      </Container>
    </section>
  );
};

export default MenuPack;
